.App{
  background: hsl(180, 52.4%, 95.9%);
  font-family: 'Spartan', sans-serif;
  padding-bottom: 120px;
  min-height: 100vh;
}

.header{
  height: 150px;
  background: hsl(180, 29%, 50%);
  background-image: url(./bg-header-mobile.svg);
  background-size: cover;
  background-repeat: no-repeat;
}


.active-tags{
  box-sizing: border-box;
  background: #FFF;
  width: 87.2%;
  max-width: 1110px;
  margin: 0 auto;
  padding-left: 19px;
  padding-right: 19px;
  border-radius: 5px;
  box-shadow: 0 15px 20px -5px hsla(189, 81.8%, 28%, 0.15);
  transform: translateY(-50%);

  &__inner{
    display: flex;
    align-items: center;

    &__tags{
      padding: 12px 0;
    }

    &__clear{
      margin-left: auto;
      font-size: 13px;
      line-height: 1.846em;
      color: hsl(180, 7.8%, 52.4%);
      cursor: pointer;

      &:hover{
        color: hsl(180, 28.9%, 50.4%);
        text-decoration: underline;
      }
    }
  }
}

.tag{
  display: inline-block;
  background: hsl(180, 31%, 95%);
  margin-right: 16px;
  margin-bottom: 8px;
  margin-top: 8px;
  padding: 0px 0px 0px 8px;
  border-radius: 4px;

  &__name{
    font-weight: 700;
    font-size: 13px;
    line-height: 1.846em;
    color: hsl(180, 28.9%, 50.4%);
    margin-right: 8px;
  }

  &__remove{
    background: hsl(180, 28.9%, 50.4%);
    padding: 9px;
    vertical-align: middle;
    border-radius: 0 4px 4px 0;
    cursor: pointer;
    transition: background 0.2s ease-out;

    &:hover{
      background: hsl(180, 14%, 19.6%);
    }
  }
}

.job{
  box-sizing: border-box;
  background: #FFF;
  width: 87.2%;
  max-width: 1110px;
  margin: 0 auto;
  border-radius: 5px;
  box-shadow: 0 15px 20px -5px hsla(189, 81.8%, 28%, 0.15);
  display: flex;
  flex-direction: column;
  margin-top: 40px;

  position: relative;
  padding: 32px 24px 24px 24px;

  &__details{
    display: flex;
    padding-bottom: 16px;
    border-bottom: 1px solid hsl(180, 9.9%, 74.3%);
    flex-shrink: 0;

    &__logo{
      position: absolute;
      align-self: center;
      width: 48px;
      top: 0;
      left: 24px;
      transform: translateY(-50%);
    }

    &__text{

      &__company{
        margin-bottom: 8px;

        &__name{
          font-size: 13px;
          font-weight: 700;
          color: hsl(180, 28.9%, 50.4%);
          margin-right: 16px;
        }

        &__new{
          text-transform: uppercase;
          font-size: 11px;
          font-weight: 700;
          color: #FFF;
          background: hsl(180, 28.9%, 50.4%);
          padding: 6px 8px 4px 8px;
          border-radius: 12px;
          margin-right: 8px;
        }

        &__featured{
          display: none;
          text-transform: uppercase;
          font-size: 11px;
          line-height: 1.273em;
          font-weight: 700;
          color: #FFF;
          background: hsl(180, 14%, 19.6%);
          padding: 6px 8px 4px 8px;
          border-radius: 12px;
        }
      }

      &__position{
        font-size: 15px;
        font-weight: 700;
        line-height: 1.6em;
        color: hsl(180, 14%, 19.6%);
        margin-bottom: 8px;
        cursor: pointer;

        &:hover{
          color: hsl(180, 28.9%, 50.4%);
        }
      }

      &__bottom{
        font-size: 13px;
        font-weight: 500;
        line-height: 1.846em;
        color: hsl(180, 7.8%, 52.4%);
        display: flex;
        align-items: center;
        width: fit-content;

        &__time{
          display: inline-block;
          margin-right: 10px;
        }

        &__status{
          display: inline-block;
          margin-right: 10px;

        }

        &__location{
          display: inline-block;

        }

        &__filler{
          display: inline-block;
          width: 4px;
          height: 4px;
          background: hsl(180, 9.9%, 74.3%);
          margin-right: 10px;
        }
      }
    }
  }

  &__tags{
    padding-top: 16px;
    
    &__tag{
      font-weight: 700;
      font-size: 13px;
      line-height: 1.846em;
      color: hsl(180, 28.9%, 50.4%);
      background: hsl(180, 31%, 95%);
      padding: 4px 8px;
      margin-right: 16px;
      margin-bottom: 16px;
      display: inline-block;
      cursor: pointer;
      transition: color .2s, background .2s;

      &:hover{
        color: #FFF;
        background: hsl(180, 28.9%, 50.4%);
      }
    }
  }
}

.job--featured{

  border-left: 5px solid hsl(180, 28.9%, 50.4%);
  
  .job__details__text__company__featured{
    display: inline;
  }
}

@media screen and (min-width: 750px){

  .header{
    background-image: url(./bg-header-desktop.svg);
  }

  .job{
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 32px 40px;
    height: 152px;

    &__details{
      border: none;
      margin-right: 19px;
      align-self: center;
      padding-bottom: 0;

      &__logo{
        position: static;
        transform: translateY(0);
        width: 88px;
        margin-right: 24px;
      }

      &__text{
        align-self: center;
      }
    }

    &__tags{
      padding-top: 0;
      align-self: center;;
      text-align: right;

      &__tag{
        margin: 8px 16px 8px 0;
      }
    }
  
  }


}